<template>
  <div>
    <div class="search-container">
      <input class="search-input" placeholder="e.g. pA110, EGFP" v-model="searchVal"/>
      <Button class="search-btn" type="warning" @click="searchAll">Search</Button>
    </div>

    <Collapse simple style="text-align: left">
      <Panel name="1">
        Advanced screening
        <div slot="content">
          <Form :model="searchResult">
            <Row :gutter="32">
              <i-col span="4" v-for="item in searchList" :key="item.field">
                <FormItem label-position="top">
                  <div slot="label" style="padding: 0 0 !important;">{{item.field}}</div>
                  <Select v-model="searchResult[item.field]" filterable multiple :transfer="true" @on-change="filterResult">
                    <Option v-for="item in item.values" :value="item" :key="item">{{ item }}</Option>
                  </Select>
                </FormItem>
              </i-col>
            </Row>
          </Form>
        </div>
      </Panel>
    </Collapse>
    <Table stripe border :columns="cols" :data="carriers">
      <template slot-scope="{ row }" slot="operator">
        <Button type="success" @click="showDetail(row.ID)" class="opt-btn">detail</Button>
        <Button type="info" @click="eidtDetail(row)" v-if="isVector" class="opt-btn">edit</Button>
        <Poptip
            confirm
            :transfer="true"
            v-if="isVector"
            title="Are you sure you want to delete this item?"
            @on-ok="deleteOk(row.ID)">
          <Button type="error"  class="opt-btn">delete</Button>
        </Poptip>
      </template>
    </Table>
    <Drawer title="edit"
            width="600"
            :mask-closable="false"
            :closable="false"
            v-model="editFlag">
      <Form>
        <Row :gutter="32">
          <i-col span="12" v-for="(value, name) in editCarr" :key="name">
            <FormItem :label="name" label-position="top">
              <Input v-model="editCarr[name]"/>
            </FormItem>
          </i-col>
        </Row>
      </Form>
      <Divider></Divider>
      <div>
        <Button style="margin-right: 8px" @click="editFlag = false">Cancel</Button>
        <Button type="primary" @click="submitEdit">Submit</Button>
      </div>
    </Drawer>
    <v-loading :show="loading"></v-loading>
  </div>
</template>

<script>
import VLoading from "@/components/public/v-loading";
export default {
  name: "carriersList",
  components: {VLoading},
  props : {
  },
  data : function (){
    return {
      searchResult :{},
      searchVal : "",
      loading : false,
      carriers : [],
      cols : [],
      currentPage : 0,
      pageSize : 0,
      total : 0,
      editFlag : false,
      editCarr : {},
      editId : "",
      searchList : [],
      carriersOld : []
    }
  },
  beforeMount() {
    this.initPage();
  },
  methods : {
    filterResult : function (){
      var newResultList = new Array();
      for (var ind=0; ind < this.carriersOld.length;ind++){
        var car = this.carriersOld[ind];
        if (this.compare(car)){
          newResultList.push(car);
        }
      }
      this.carriers = newResultList;
    },
    compare : function (car){
      var isAllEmpty = true;
      for (var item in this.searchResult){
        for (var i =0; i< this.searchResult[item].length; i++){
          isAllEmpty = false;
          if (car[item] === this.searchResult[item][i]){
            return true;
          }
        }
      }
      if (isAllEmpty){
        return true;
      }
      return false;
    },
    searchAll : function (){
      this.initPage();
    },
    submitEdit : function (){
      this.editCarr.id = this.editId;
      var postData = {
        carrier : JSON.stringify(this.editCarr)
      }
      this.loading = true;
      var _this = this;
      this.$axios.post("/snapGene/commitCarrier",postData).then(function (res){
        _this.loading = false;
        _this.editFlag = false;
        console.log(res);
        _this.initPage();
        _this.$Message.success("submit success");
      }).catch(function (res){
        _this.loading = false;
        _this.editFlag = false;
        _this.$Message.error("system error!");
        console.log(res);
      })
    },
    deleteOk :function (id){
      console.log(id);
      this.loading = true;
      var postData = {
        fileId : id
      };
      var _this = this;
      this.$axios.post("/snapGene/deleteCarrier", postData).then(function (res){
        console.log(res);
        _this.loading = false;
        _this.$Message.success("delete success");
        _this.initPage();
      }).catch(function (res){
        _this.loading = false;
        console.log(res);
      })
    },
    eidtDetail : function (row){
      this.editId = row.ID;
      for (var name in row){
        if (name == "_id" || name == "ID" || name == "_index" || name == "_rowKey" || name == "id"){
          continue;
        }
        this.editCarr[name] = row[name];
      }
      this.editFlag = true;
    },
    showDetail : function (id){
      this.$router.push({name:"carrier", query:{id:id}});
    },
    initPage : function (){
      this.initCols();
      var postData = {
        currentPage : this.currentPage,
        pageSize : this.pageSize,
        searchVal : this.searchVal
      }
      var _this = this;
      this.loading = true;
      this.$axios.post("/home/initPage", postData).then(function (res){
        console.log(res)
        _this.loading = false;
        _this.carriers = res.data.retMap.carriers;
        _this.carriersOld = res.data.retMap.carriers;
        _this.total = res.data.retMap.total;
        _this.searchList = res.data.retMap.searchList;
        for (var i = 0; i < _this.searchList.length; i++){
          var sec = _this.searchList[i];
          _this.searchResult[sec.field] = new Array();
        }
      }).catch(function (res){
        _this.loading = false;
        console.log(res);
      })
    },
    initCols :function (){
      var cols = new Array();
      cols.push({
        type: 'selection',
        width: 60,
        align: 'center'
      });
      cols.push({
        title : this.$t("CAT"),
        key : "CAT",
        width: 100,
        sortable: true
      });
      cols.push({
        title : this.$t("NAME"),
        key : "NAME",
        sortable: true
      })
      // cols.push({
      //   title : this.$t("BACKBONE"),
      //   key : "BACKBONE",
      //   width: 200,
      //   sortable: true
      // })

      cols.push({
        title : this.$t("ADDGENE"),
        key : "ADDGENE",
        width: 200,
        sortable: true
      })
      cols.push({
        title : this.$t("PROMOTER"),
        key : "PROMOTER",
        sortable: true
      })
      cols.push({
        title : this.$t("EXPRESSIONCASSETTE"),
        key : "EXPRESSIONCASSETTE",
        sortable: true
      })
      cols.push({
        title : this.$t("operator"),
        slot : "operator",
        width: 250,
      })
      this.cols = cols;
    }
  },
  computed : {
    isVector : function (){
      if (this.$store.getters.getName === undefined){
        return false;
      }
      if (this.$store.getters.isVector && this.$store.getters.isReviewer){
        return true;
      }else {
        return false;
      }
    }
  }
}
</script>

<style scoped>
.opt-btn{
  display: inline-block;
  margin: 2px 2px;
}
.demo-drawer-footer{
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
  border-top: 1px solid #e8e8e8;
  padding: 10px 16px;
  text-align: right;
  background: #fff;
}
.search-container{
  display: inline-block;
  width: 60%;
  height: 10vh;
  border: 1px solid darkgray;
  border-radius: 10px;
  line-height: 10vh;
  margin-top: 4vh;
  text-align: left;
  margin-bottom: 50px;
}
.search-input{
  display: inline-block;
  width: 80%;
  height: 80%;
  border: none;
  outline:none;
  margin-left: 5%;
}
.search-btn{
  display: inline-block;
  width: 10%;
  height: 6vh;
  font-size: 16px;
}
</style>